.ContactsBlock {
    min-height: 100vh;
    background-color: #ff711a;
}

.ContactsContainer {
    flex-direction: column;
    justify-content: flex-start;
}

.h1 {
    background-color: #ff14be;
}

.Form {
    display: flex;
    border: 1px solid;
    flex-direction: column;
    gap: 10px;
    width: 30%;
}

.Button {
    background-color: gray;
    border: none;
    color: black;
    padding: 15px 32px;
    text-align: center;
    font-size: 30px;
    cursor: pointer;
    display: flex;
    margin-top: 40px;
}