.RemoteWorkBlock {
    min-height: 30vh;
    background-color: darkcyan;
    align-items: center;
    display: flex;
}

.RemoteWorkContainer {
    justify-content: center;
    flex-direction: column;
    gap: 40px
}
.h1 {
    background-color: #9a87ff;
}
.button {
    background-color: gray;
    border: none;
    color: black;
    padding: 15px 32px;
    text-align: center;
    font-size: 30px;
    cursor: pointer;
    display: flex;

}