.projectsBlock {
    min-height: 100vh;
    background-color: #fff113;
}
.title {
    background-color: #008bff;
}
.projectsContainer {
    justify-content: flex-start;
    flex-direction: column;
}
.projects {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 24px;
}