.project {
    width: 500px;
    background-color: #26b7ff;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.button {
    background-color: gray;
    border: none;
    color: black;
    padding: 15px 32px;
    text-align: center;
    font-size: 16px;
    cursor: pointer;
    display: flex;

}

.PandW_Block {
    height: 250px;
    width: 100%;
    background-color: #ffb1aa;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Picture {
    align-self: flex-start;
    font-size: 20px;
    margin: 25px;
}

.Title_Des {
    display: flex;
    flex-direction: column;
   align-items: flex-start;
    justify-content: flex-start;
}
.h2 {
    background-color: darkmagenta;
}
.span {
    background-color: darkmagenta;
}